import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue';

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/ListNotes',
    name: 'ListNote',
    component: () => import('../views/ListNote.vue')
  },
  {
    path: '/NewNotes',
    name: 'NewNote',
    component: () => import('../views/NewNote.vue')
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
